<template>
  <div class="p_symptom_search p_home">
    <div class="go_back" @click="$router.push('/')">
      <i class="iconfont iconzuo"></i>
      <span class="icon_text">返回</span>
    </div>
    <div class="result">搜索结果共720条</div>
    <div class="frequency">
      <div class="acupoint" @click="acupointBtn">
        <div class="inner_border">
          <span class="contain_text">穴位频次</span>
        </div>
      </div>
      <div class="symptoms" @click="acupointBtn">
        <div class="inner_border">
          <span class="contain_text">病症频次</span>
        </div>
      </div>
    </div>
    <div class="search_chapter">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div
          class="preview"
          :show="show"
          @click="show = true"
          v-for="(item, index) in list"
          :key="index"
        >
          <h3 class="h_title">《素问》 第一章节</h3>
          <div class="d_content">
            <p class="p_content">
              {{
                "夫冬伤寒气在于腠理者，以冬强勇按跷，多劳困，腠理开，寒气入客。今冬不作按跷，则无伤寒，至春不患热病鼽衄，故春不病颈项者也。夫冬伤寒气在于腠理者，以冬强勇按跷，多劳困，腠理开，寒气入客。今冬不作按跷，则无伤寒，至春不患热病鼽衄，故春不病颈项者也。"
                  | ellipsis
              }}
            </p>
            <span class="open"
              >展开更多<i class="iconfont iconxia i_open"></i
            ></span>
          </div>
          <span class="classify">所属分类：医经>内经>素问</span>
        </div>
      </van-list>
    </div>

    <van-overlay :show="show">
      <div class="wrapper">
        <div class="block">
          <i class="iconfont iconcha close" @click="show = false"></i>

          <span class="classifys">所属分类：医经>内经>素问</span>
          <h3 class="h_titles">《素问》 第一章节</h3>
          <p class="p_contents">
            【原文】昔在黄帝，生而神灵，弱而能言幼而徇齐，长而敦敏，成而登天。乃问于天师曰：余闻上古之人，春秋皆度百岁，而动作不衰；今时之人，年半百而动作皆衰，时世异耶？人将失之耶？岐伯对曰：上古之人，其知道者，法于一陰一陽一，和于术数，食饮有节起居有常，不妄作劳，故能形与神俱，而尽终其天年，度百岁乃去。
          </p>

          <div class="bottom_button">
            <div class="previous_page">上一段</div>
            <div class="next_page">下一段</div>
          </div>
        </div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import { Overlay } from "vant";
export default {
  data() {
    return {
      show: false,
      list: [],
      loading: false,
      finished: false,
    };
  },
  filters: {
    ellipsis(value) {
      if (!value) return "";
      if (value.length > 60) {
        return value.slice(0, 60) + "...";
      }
      return value;
    },
  },
  created() {},
  methods: {
    acupointBtn() {
      this.$router.push({ name: "statistics" });
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      setTimeout(() => {
        for (let i = 0; i < 4; i++) {
          this.list.push(this.list.length + 1);
        }

        // 加载状态结束
        this.loading = false;

        // 数据全部加载完成
        if (this.list.length >= 6) {
          this.finished = true;
        }
      }, 1000);
    },
  },
};
</script>


<style lang="scss" scoped>
@import "../../../assets/styles/currency/index.scss";
@import "../../../assets/styles/searchlist/index.scss";
</style>


